@font-face {
  font-family: "Lato";
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: calc(100% - 40px);
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
    width: calc(100% - 60px);
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0 110px;
    width: 1220px;
  }
}

.container-large {
  margin: 0 auto;
  width: 1440px;
}

.page {
  height: 100%;
  background-color: #ffffff;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  margin: 0 auto;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-family: "Lato", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-style: normal;
  word-break: break-word;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 18px;
    line-height: 24px;
    color: #000000;
  }
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.page-header__main-nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
}
@media (max-width: 767px) {
  .page-header__main-nav {
    padding: 10px 0 18px;
  }
}
@media (min-width: 1440px) {
  .page-header__main-nav {
    padding-top: 53px;
  }
}

.logo--mobile {
  position: relative;
  margin-right: 20px;
  width: calc(100% - 24px - 20px);
}
@media (min-width: 768px) {
  .logo--mobile {
    display: none;
  }
}

.logo__image {
  margin: 0;
  width: 34px;
  height: 38px;
}

.logo__inscription {
  position: absolute;
  left: 50%;
  bottom: 8px;
  margin: 0;
  display: block;
  width: 101px;
  height: 18px;
  transform: translateX(-50%);
}

.logo--tablet {
  display: none;
  width: 174px;
  height: 52px;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .logo--tablet {
    display: block;
  }
}

.logo--desktop {
  display: none;
  width: 202px;
  height: 59px;
}
@media (min-width: 1440px) {
  .logo--desktop {
    display: block;
  }
}

.logo:hover {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}

.page-header-toggle {
  margin: 0 0 10px;
  padding: 8px 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.page-header-toggle__burger {
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
@media (min-width: 768px) {
  .page-header-toggle__burger {
    display: none;
  }
}

.page-header-toggle__burger::before {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translateY(-7px);
}

.page-header-toggle__burger::after {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translateY(5px);
}

.site-menu {
  margin-left: auto;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 767px) {
  .site-menu {
    display: none;
  }
}
@media (min-width: 768px) {
  .site-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 35px;
    max-width: 460px;
  }
}
@media (min-width: 1440px) {
  .site-menu {
    gap: 34px;
    max-width: 720px;
  }
}

.site-menu__link {
  margin: 0;
  font-family: "Oswald", Arial, sans-serif;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
  padding: 8px 0;
}
@media (min-width: 768px) {
  .site-menu__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}
@media (min-width: 1440px) {
  .site-menu__link {
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
  }
}